import React from 'react';
import { Link } from 'react-router-dom';
const POP = () => {
    return (
        <div  >
               <div className = "popup_img" ><img src="./img/gareNC.png" alt="logo" /></div> 
              <div className="popup_title"><h1> Gare de New-Cherbourg</h1></div> 
              <div className="popuplink"><Link className="Pop_button"to="/Batiments" > Decouvir... </Link> </div>  
        
        </div>
    );
};

export default POP;