import React , {useState} from 'react';
import ReactMapGl, {Marker, Popup} from 'react-map-gl';
import { NavLink } from 'react-router-dom';
import { Link } from 'react-router-dom';
import POP from './Popup';




const Carte = () => {



    const [viewport , setViewport] =  useState({
        latitude : 49.63349446077185,
        longitude : -1.6214694296580212,
        width:"100vw",
        height:"100vh",
        zoom :12,
       
      });

      const MAPBOX_TOKEN = 'pk.eyJ1IjoiZGF2aWRkZDEiLCJhIjoiY2tvYTA5eGZuMHp3eDJvbXV1dzhnemRzdyJ9.9piPbHzUKZsQ23CDUg3U0g'; // Set your mapbox token here

      const [showPopup, togglePopup] = React.useState(false);



    return (
        <div className="carte">
    
        <ReactMapGl
          {...viewport}
          onViewportChange={nextViewport => setViewport(nextViewport)}
          mapboxApiAccessToken={MAPBOX_TOKEN}
          mapStyle='mapbox://styles/mapbox/streets-v11'
          >
            
          {showPopup &&
          <Popup className="popup"
            latitude={49.63349446077185}
            longitude={-1.6214694296580212}
            closeOnClick={false}
            onClose={() => togglePopup(false)}
           
            >
              <POP/>

          </Popup>} 
          <Marker latitude={49.63349446077185} longitude={-1.6214694296580212} offsetLeft={-20} offsetTop={-10}>

                 

                  <button
                   className="marker-btn"
                  onClick={e => {
                    
                     togglePopup(true);
              }}
            >

                <div className="marqueur">    
                <img src="./img/locator-version-4-md(1).png" alt="logo" />
                
                </div>
            </button>
                  

                 
          </Marker>




        </ReactMapGl>
        </div>
    );
};

export default Carte;



